.cart-block {
  overflow: hidden;
  padding-bottom: 40px;
  .cart-item {
    width: 400px !important;
    transition: opacity 0.2s;
    &.opacity {
      opacity: 0.2;
    }
  }
}

.cart-slider {
  position: relative !important;
  .swiper-button-prev {
    left: -40px;
  }
  .swiper-button-next {
    right: -40px;
  }
}

