.intro-block.with-search {
  padding: 8px 0px 20px 0px;
  overflow: hidden;
  .breadcrumbs {
    align-self: flex-start;
    margin-bottom: 40px;
    @media screen and (max-width: $mobileL) {
      margin-bottom: 16px;
    }
  }
  .main-title {
    margin-bottom: 20px;
    @media screen and (max-width: $mobileL) {
      max-width: 295px;
      width: 100%;
    }
  }
  .search {
    margin-top: 20px;
  }
}

// Categories Block
.categories-block {
  padding: 20px 0px 40px 0px;
  .mini-title {
    margin-bottom: 20px;
    align-self: flex-start;
  }
  &__list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 20px 1.5625%;
    @media screen and (max-width: $tablet) {
      gap: 16px 4.5%;
    }
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  &__item {
    width: 12.86875%;
    @media screen and (max-width: $laptopM) {
      width: 15%;
    }
    @media screen and (max-width: $laptopS) {
      width: 18.55%;
    }
    @media screen and (max-width: $tablet) {
      width: 30.18%;
    }
  }
  .container:last-child:not(:first-child) {
    padding-top: 20px;
  }
}

// Select Category Block
.select-bar {
  background: #EEEFF1;
  padding: 20px 0px;
  @media screen and (max-width: $tablet) {
    padding: 16px 0px;
  }
  &__item {
    margin-right: 40px;
    @media screen and (max-width: $tablet) {
      margin-right: 8px;
    }
  }
  .container {
    flex-direction: row;
    justify-content: flex-start;
  }
}

// Products Block Styles
.products-block {
  padding-top: 0;
  padding-bottom: 40px;
  .products {
    width: 100%;
    display: flex;
    align-items: center;
    gap: 40px 3.125%;
    flex-wrap: wrap;
    .product {
      width: 17.5%;
      @media screen and (max-width: $laptopS) {
        width: 31.1%;
      }
      @media screen and (max-width: $tablet) {
        width: 48%;
      }
    }
  }
  .container {

  }
}