.header {
  padding: 15px 0px;
  @media screen and (max-width: $tablet) {
    padding: 0;
  }
  .container {
    display: flex;
    align-items: center;
    @media screen and (max-width: $tablet) {
      padding-right: 0;
    }
  }
  .nav {
    @media screen and (max-width: $tablet) {
      display: none;
    }
  }
  &__logotype {
    margin-right: 92px;
    @media screen and (max-width: $laptopM) {
      margin-right: 62px;
    }
    @media screen and (max-width: $laptopS) {
      margin-right: 40px;
    }
    @media screen and (max-width: $tablet) {
      display: none;
    }
    img {
      width: 133px;
      @media screen and (max-width: $laptopS) {
        width: 90px;
      }
    }
  }
  &__cart {
    margin-right: 56px;
    margin-left: auto;
    @media screen and (max-width: $laptopS) {
      margin-right: 36px;
    }
    @media screen and (max-width: $tablet) {
      margin-right: 8px;
    }
  }
  &__account-button {
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: $text-color;
    @media screen and (max-width: $laptopS) {
      font-size: 14px;
    }
    @media screen and (max-width: $tablet) {
      display: none;
    }
  }

  .mb-account-button {
    display: none;
    @media screen and (max-width: $tablet) {
      background: $yellow;
      width: 56px;
      height: 56px;
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        fill: white;
        width: 20px;
        height: 22px;
      }
    }
  }

  .mb-burger {
    display: none;
    @media screen and (max-width: $tablet) {
      display: block;
      width: 24px;
      height: 24px;
      svg {
        fill: $yellow;
        height: 100%;
        width: 100%;
      }
    }
  }
  .mb-current-page {
    display: none;
    @media screen and (max-width: $tablet) {
      display: inline;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 22px;
      color: $text-color;
      margin-left: 16px;
    }
  }
}

.cart-label {
  display: flex;
  align-items: center;
  font-size: 16px;
  position: relative;
  @media screen and (max-width: $laptopS) {
    font-size: 14px;
  }
  @media screen and (max-width: $tablet) {
    font-size: 12px;
  }
  &__icon {
    width: 1.5em;
    height: 1.5em;
    fill: $text-color;
    margin-right: 0.625em;
    @media screen and (max-width: $tablet) {
      width: 2em;
      height: 2em;
      fill: $yellow;
    }
  }
  &__count {
    font-style: normal;
    font-weight: 600;
    font-size: inherit;
    line-height: 1.375em;
    color: $text-color;
    span {
      @media screen and (max-width: $tablet) {
        display: none;
      }
    }
    mark {
      @media screen and (max-width: $tablet) {
        position: absolute;
        top: -6px;
        right: 0;

        font-weight: bold;
        font-size: 12px;
        text-align: center;
        letter-spacing: 0.14px;
        color: #FFFFFF;
        width: 16px;
        height: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: $accent;
        border-radius: 10px;
      }
    }
  }
}